import ClipboardJS from 'clipboard';
import axios from 'axios';


export default {
    name: 'Product',
    mounted() {

        var clipboard = new ClipboardJS('.invitecode');
        clipboard.on('success', (e) => {
            e.clearSelection();
            // clipboard.destroy();
            this.$util.doClipboardCallback();
        });

        this.getBanner();
        this.getProductDetail();
        this.getCountryCode();
        this.shareid = this.$util.getUrlParam('shareid');
    },
    data() {
        return {
            productData: '',
            topProductData: '',
            switchImg: true,
            countryCode: 'CN',
            shareid: '',
            banner: '',
            messages: {
                en: {
                    tweebaa: 'Tweebaa',
                    hi: "Hi,I'm ",
                    share: 'I have good products to share with you',
                    banner: 'Share good products in tweebaa',
                    downloadApp: 'Download Tweebaa App Get Tweebucks',
                    topProduct: 'Top Product',
                    tweebucks: 'Tweebucks',
                    approximateProfit: 'Approximate Profit',
                    maxProfit: 'Maximum Rewards',
                    platform: 'Tweebaa is the world’s 1st multi-dimensional value-exchange social networking platform. Designed for creating value from non-traditional products, ideas, and your personal influence; Tweebaa allows anyone to earn on a large scale with zero financial investment. With the tools we provide, anyone cancreate a personalized foundation for their business completely free and suggest, shop, or share products with people all over the world and earn money.',
                    name: 'Name',
                    price: 'Price',
                    desc: 'Summary',
                    detail: 'Description'
                },
                zh: {
                    tweebaa: '推易吧',
                    hi: 'Hi,我是',
                    share: '我有好产品和你分享哦',
                    banner: '搜好物 推易吧 创意无限',
                    downloadApp: '下载推易吧App 获取T豆奖励',
                    topProduct: '热门产品',
                    tweebucks: 'T豆',
                    approximateProfit: '您的奖励约为',
                    maxProfit: '最高销售奖励',
                    platform: '推易吧是一个让您利用自己的知识、经验、技巧、兴趣和人脉来变现的平台。推易吧会为您提供多样化的创意产品和工具，让您轻松开始零成本、零库存的创业之旅。',
                    name: '产品名称',
                    price: '产品售价',
                    desc: '产品简介',
                    detail: '详细介绍'
                },
            }
        };
    },

    filters: {
        fixed2(value) {
            return Number(value).toFixed(2);
        }
    },
    methods: {
        getBanner() {
            var i = Math.floor(Math.random() * 4) + 1;
            try {
                this.banner = require('../../image/banner' + i + '-' + this.$util.language + '.jpg');
            } catch (e) {

            }
        },
        getCountryCode() {
            var that = this;
            // $.getJSON("http://ip-api.com/json/?callback=?", function (data) {
            //     that.countryCode = data.countryCode;
            // });
            axios.get("https://api.ipdata.co/?api-key=bd616afaef799bfe15627291f24ad3fa83c897a6a1219caa8c42928b") // 使用eric.guo@leivaire.com 申请的code
                .then(function (response) {
                    console.log(response);
                    that.countryCode = response.data.country_code;
                })
                .catch(function (error) {
                    console.log(error);
                });
        },
        getProductDetail() {
            var that = this;
            var userId = this.$util.getUrlParam('userid');
            var prodid = this.$util.getUrlParam('prodid');
            var url = this.$util.getBaseUrl() + "api/users/" + userId + "/products/" + prodid;
            this.$util.getAxois(url, "GET", {}).then(function (response) {
                var div = document.getElementById("code");
                div.dataset.clipboardText = that.shareid;
                that.productData = response.data;
                var loading = document.getElementById('loadingText');
                var parentObj = loading.parentNode;
                parentObj.removeChild(loading);
                setTimeout(function () {
                    initialize();
                    that.getAllPorducts(response.data.Owner.Id);
                    var url = that.$util.getBaseUrl() + "/api/data-analytics/save-view-data";
                    var pram = {
                        TypeId: 2,
                        ProductId: prodid,
                        UserId: userId,
                        CountryCode: that.countryCode,
                        StayTimeSeconds: Math.floor(Math.random() * (120 - 10)) + 10
                    };
                    //console.log(JSON.stringify(pram));
                    that.$util.getAxois(url, "POST", pram).then(function (response) {
                        //console.log(JSON.stringify(response.data));
                    }).catch(function (error) {
                        console.log(JSON.stringify(error) + '#######################');
                    });
                });
            }).catch(function (error) {
                console.log(error);
            });
        },
        getAllPorducts(ownerId) {
            var that = this;
            var url = this.$util.getBaseUrl() + "/api/v2/users/products";
            var paramStr = {
                "ownerId": ownerId,
                "currentpage": 0,
                "itemsperpage": 3
            };
            this.$util.getAxois(url, "POST", paramStr).then(function (response) {
                //console.log(JSON.stringify(response));
                that.topProductData = response.data.Data;

            }).catch(function (error) {
                console.log(JSON.stringify(error));
                //messages($.i18n.prop('systemError'), 'error', 'top', 3);
            });
        },
        switchImgVideo(type) {
            this.switchImg = type === 'img';
            initialize();
        },
        gotoDownload() {
            document.getElementById("code").click();
            setTimeout(function () {
                if (getPhoneSystem() === 'ios') {
                    window.location.href = 'https://apps.apple.com/us/app/tweebaa/id1467778439';
                } else if (getPhoneSystem() === 'Android') {
                    window.location.href = 'weixin.html';
                }
            }, 1000)
        }
    }
}

//初始化图片显示
function initialize() {
    new Swiper('#proDetailPhoto', {
        direction: 'horizontal',
        loop: false,
        initialSlide: 0,
        pagination: {
            el: '.swiper-pagination',
            type: 'bullets'
        },
        dynamicBullets: true
    });
}
